.big{
  font-size: 20px;
  width: 100%;
  height: 50px;
  border-radius: 10px;
  border: 1px solid rgb(0, 0, 0);
  background-color: rgb(234, 234, 234);
  overflow: hidden;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: end;
  margin: 5px;
}
.big1{
  width: 100%;
  height: 250px;
  display: grid;
  grid-template-columns: repeat(100%, 4);
  padding: 5px;
}
.B{
  
  display: flex;
  justify-content:space-evenly;
}
button{
  font-size: larger;
  width: 60px;
  height: auto;
  margin: 5px;
  border-radius: 10px;
}
button:hover{
  background-color: aliceblue;
}